import anime from 'animejs';
import {
  nativeSmoothScroll
} from '@smoovy/scroller';
import charming from 'charming';

// -------------------------------------------

const ayushimage = require('./static/resized/ayush.webp')
const wynterimage = require('./static/resized/anokha.webp')
const juliusimage = require('./static/resized/julius.webp')
const faerieimage = require('./static/resized/faerie.webp')
const fingerprintimage = require('./static/resized/fingerprint.webp')
const newsimage = require('./static/resized/dribble-hackernews.webp')
const moreimage = require('./static/mecode.webp')

// -------------------------------------------


let scroller;
let images = [],
  parallax = [],
  mat = [];
let tl;
let deviceType;

const throttle = (fn, delay = 16.6) => {
  let timer = null;
  return () => {
    let context = this,
      args = arguments;
    clearTimeout(timer);
    timer = setTimeout(() => {
      fn.apply(context, args);
    }, delay);
  };
};

const lerp = (a, b, n) => (1 - n) * a + n * b;

const detectDevice = (screenWidth) => {
  if (screenWidth < 768) {
    deviceType = 'mobile'
  } else if (screenWidth < 1023) {
    deviceType = 'tablet'
  } else if (screenWidth < 1215) {
    deviceType = 'desktop'
  } else if (screenWidth < 1407) {
    deviceType = 'widescreen'
  } else if (screenWidth >= 1408) {
    deviceType = 'fullhd'
  }
  return deviceType
}

// ----------------------------------------------------------


document.addEventListener('DOMContentLoaded', () => {
  // reset scroll position
  window.scrollTo({
    top: 0,
    left: 0,
  });
  // select text elements to animate
  let words = document.querySelectorAll('.text-wrap')
  words.forEach(word => {
    charming(word, {
      setClassName: (index, letter) => {
        return `text-element`
      }
    })
  })
  // clean up empty nodes
  words = document.querySelectorAll('.text-element')
  words.forEach(word => {
    if (word.innerText === '') {
      word.remove()
    }
  })
  // initialise stuff
  const wrapper = document.getElementById('scroll-wrapper');
  scroller = nativeSmoothScroll({
    element: wrapper
  });
  if (deviceType != 'mobile' && deviceType != 'tablet') {
    mouseOvers()
    initCursor()
  }
  scrollAnims();
})

window.onload = () => {
  let screenWidth = window.innerWidth
  deviceType = detectDevice(screenWidth);
  const crsr = document.getElementById('crsr')

  tl = anime.timeline({})

  tl.add({
    targets: '.preloader-name',
    translateY: [0, '110%'],
    duration: 700,
    easing: 'easeInQuint',
    delay: anime.stagger(50),
  })

  tl.add({
    targets: '#loader',
    opacity: 0,
    duration: 500,
    easing: 'easeInExpo',
    complete: () => {
      render()
      document.getElementById("loader").remove();
      heroAnims();
    }
  }, 400);
  // select elements to animate later
  images = Array.from(document.querySelectorAll('[data-scroll-skew]'));
  parallax = Array.from(document.querySelectorAll('[data-scroll-parallax]'));
  mat = Array.from(document.querySelectorAll('[data-mat]'));
};

// -------------------------------------------


const heroAnims = () => {
  tl = anime.timeline()

  tl.add({
    targets: '.herostuff',
    translateY: ['110%', 0],
    translateZ: '1px',
    opacity: [0, 1],
    duration: 1750,
    easing: 'easeOutExpo',
    delay: anime.stagger(250)
  }, 1000)

  tl.add({
    targets: '.herodeco',
    translateY: ['110%', 0],
    opacity: [0, 0.5],
    translateZ: '1px',
    duration: 1750,
    easing: 'easeOutExpo',
    delay: anime.stagger(250)
  }, 500)

  tl.add({
    targets: '.text-element',
    translateY: ['110%', 0],
    delay: anime.stagger(50),
    easing: 'easeOutExpo',
    duration: 1500
  }, 0)

}

let scroll = 0,
  imgRounded = 0,
  imgSkew = 0
const scrollAnims = () => {
  let down = true,
    lastScrollY = 0,
    skew = 0,
    rounded = 0

  let h = document.documentElement,
    b = document.body,
    st = 'scrollTop',
    sh = 'scrollHeight';

  // scroll animation values
  scroller.onScroll(position => {

    down = lastScrollY < position.y
    skew = (lastScrollY - position.y) * 0.75
    rounded = Math.round(skew * 1.5)
    lastScrollY = position.y;

    // calculate skew on scroll
    imgSkew = skew > 0 ? Math.min(skew, 30) : Math.max(skew, -30)
    imgRounded = rounded > 0 ? Math.min(rounded, 30) : Math.max(rounded, -30)

    scroll = (h[st] || b[st]) / ((h[sh] || b[sh]) - h.clientHeight);

  })
}


let crsrX = 0;
let crsrY = 0;
let drsrX = 0;
let drsrY = 0;

const mouseOvers = () => {
  let links = Array.from(document.querySelectorAll('[data-mouseover]'))
  const table = {
    "ayush": ayushimage,
    "wynter": wynterimage,
    "julius": juliusimage,
    "faerie": faerieimage,
    "fingerprint": fingerprintimage,
    "hackernews": newsimage,
    "more": moreimage
  }

  links.forEach(el => {
    el.addEventListener('mouseenter', event => {
      crsr.setAttribute('class', 'imgcursor visible')
      crsr.src = table[el.id]
      el.addEventListener('mousemove', move => {
        crsrX = move.clientX
        crsrY = move.clientY
      }, {
        passive: true
      })
    })
    el.addEventListener('mouseleave', event => {
      crsr.setAttribute('class', 'imgcursor')
      el.removeEventListener('mousemove', () => console.log('cleaned'))
    })
  })
}

// cursor stuff
let sx = 0;
let sy = 0;
let dx = 0;
let dy = 0;
const customcursor = document.getElementById('customcursor')
const alinks = document.querySelectorAll('a')
const initCursor = () => {
  document.addEventListener('mousemove', move => {
    sx = move.clientX;
    sy = move.clientY;
  }, {
    passive: true
  })
  alinks.forEach(el => {
    el.addEventListener('mouseenter', event => {
      customcursor.setAttribute('class', 'cursor cursor-hover')
    })
    el.addEventListener('mouseleave', event => {
      customcursor.setAttribute('class', 'cursor')
    })
  })
}


// render loop
const render = () => {
  // lerp customcursor
  dx = lerp(dx, sx, 0.1);
  dy = lerp(dy, sy, 0.1);
  customcursor.setAttribute("style", "transform: translate3d(" + dx + "px, " + dy + "px, 0)")

  // lerp image cursor
  drsrX = lerp(drsrX, crsrX, 0.2)
  drsrY = lerp(drsrY, crsrY + window.scrollY, 0.2)
  crsr.setAttribute("style", "transform: translate3d(" + (drsrX + 20) + "px, " + (drsrY) + "px, 1px)")

  // skew images
  images.forEach(el => {
    el.style.transform = `translate3d(0, ${imgRounded}px, 1px) skewY(${imgSkew}deg)`
  })
  // apply parallax effect on scroll
  parallax.forEach(el => {
    el.style.transform = `translate3d(0, -${scroll*75}%, 1px)`;
  })

  // scale and transform images on scroll
  mat.forEach(el => {
    el.style.transform = `translate3d(0, -${scroll*25}%, 1px) scale(${1+(scroll)*0.25}, ${1+(scroll)*0.25})`;
  })

  // progress bar
  progress.style['transform'] = `scaleX(${scroll})`;

  requestAnimationFrame(render)
}